import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import Videogame from "../videogame";
import { Link } from "react-router-dom";
import axios from "axios";
import { GENRES_URL } from "../../constants";

import { useDispatch } from "react-redux";
import { getVideogames } from "../../actions/index";

export default function Videogames() {
  var videogames = useSelector((state) => state.videogames);
  const dispatch = useDispatch();
  const [filteredVideogames, setFilteredVideogames] = useState([]);
  const [genres, setGenres] = useState([]);

  useEffect(() => {
    dispatch(getVideogames());
  }, [dispatch]);

  function getGenres() {
    axios.get(GENRES_URL).then((response) => {
      setGenres(response.data);
    });
  }

  useEffect(() => {
    setFilteredVideogames(videogames);
  }, [videogames]);

  useEffect(() => {
    getGenres();
  }, []);

  function change1(e) {
    let aux = [...filteredVideogames];
    if (e.target.value === "home") {
      setFilteredVideogames(videogames);
    }
    if (e.target.value === "A-Z") {
      aux.sort((a, b) => {
        if (a.name > b.name) {
          return 1;
        } else {
          return -1;
        }
      });
      setFilteredVideogames([...aux]);
    }
    if (e.target.value === "Z-A") {
      aux.sort((a, b) => {
        if (a.name < b.name) {
          return 1;
        } else {
          return -1;
        }
      });
      setFilteredVideogames([...aux]);
    }
    if (e.target.value === "Mayor Rating") {
      aux.sort((a, b) => {
        if (a.rating < b.rating) {
          return 1;
        } else {
          return -1;
        }
      });
      setFilteredVideogames([...aux]);
    }
    if (e.target.value === "Menor Rating") {
      aux.sort((a, b) => {
        if (a.rating > b.rating) {
          return 1;
        } else {
          return -1;
        }
      });
      setFilteredVideogames([...aux]);
    }
  }

  function change2(e) {
    let aux = [...videogames];
    if (e.target.value === "All") {
      setFilteredVideogames([...aux]);
    } else {
      let aux2 = aux.filter((videogame) => {
        let genreCheck = videogame.genres.map((e) => e.name);
        return genreCheck.includes(e.target.value);
      });

      setFilteredVideogames([...aux2]);
    }
  }

  return (
    <div>
      <select name="selectBox" onChange={(e) => change1(e)}>
        <option value="home">All</option>
        <option value="A-Z">A-Z</option>
        <option value="Z-A">Z-A</option>
        <option value="Mayor Rating">Mayor Rating-Menor Rating</option>
        <option value="Menor Rating">Menor Rating-Mayor Rating</option>
      </select>

      <select name="selectBox" onChange={(e) => change2(e)}>
        <option value="All">All</option>
        {genres.map((e) => {
          return <option value={e.name}>{e.name}</option>;
        })}
      </select>

      {filteredVideogames.map((videogame) => {
        return (
          <div key={videogame.id}>
            <Link to={`/home/videogame/${videogame.id}`}>
              <Videogame
                name={videogame.name}
                background_image={videogame.background_image}
                genres={videogame.genres}
                key={videogame.id}
              />
            </Link>
          </div>
        );
      })}
    </div>
  );
}
