import React from "react";
import estilo from "./videogame.module.css";

export default function Videogame({ name, background_image, genres }) {
  return (
    <div className={estilo.content}>
      <div className={estilo.card}>
        <p>{name}</p>

        <img className={estilo.img} src={background_image} alt={name} />

        <p>
          {genres.map((e) => {
            return `${e.name} `;
          })}
        </p>
      </div>
    </div>
  );
}
