import React, { useEffect, useState } from "react";
import axios from "axios";
import { GENRES_URL, VIDEOGAMES_URL } from "../../constants";

export default function CreateVideogame() {
  const [videogame, setVideogame] = useState({
    name: "",
    description: "",
    background_image: "",
    plataforms: [],
    released: "",
    rating: 0,
    genres: [],
  });
  const [genres, setGenres] = useState([]);

  function getGenres() {
    axios.get(GENRES_URL).then((response) => {
      setGenres(response.data);
    });
  }

  useEffect(() => {
    getGenres();
  }, []);

  function onInputChange(e) {
    setVideogame((prevState) => {
      return {
        ...prevState,
        [e.target.name]: e.target.value,
      };
    });
  }

  function addGenres(e, id) {
    e.preventDefault();
    setVideogame(() => {
      return {
        ...videogame,
        genres: [...videogame.genres, id],
      };
    });
  }

  async function handleSubmit(e) {
    e.preventDefault();
    await axios.post(VIDEOGAMES_URL, videogame);
    alert("Se Agrego");
  }

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <div>
          <label htmlFor="name">Name:</label>
          <input
            name="name"
            value={videogame.name}
            type="text"
            onChange={onInputChange}
          />
        </div>
        <div>
          <label htmlFor="description">Description:</label>
          <input
            name="description"
            value={videogame.description}
            type="text"
            onChange={onInputChange}
          />
        </div>
        <div>
          <label htmlFor="background_image">Image:</label>
          <input
            name="background_image"
            value={videogame.background_image}
            type="text"
            onChange={onInputChange}
          />
        </div>
        {/* <div>
          <label htmlFor="plataforms">platforms:</label>
          <input
            name="plataforms"
            value={videogame.plataforms}
            type="text"
            onChange={onInputChange}
          />
        </div> */}
        <div>
          <label htmlFor="rating">rating:</label>
          <input
            name="rating"
            value={videogame.rating}
            type="number"
            min="0"
            max="5"
            onChange={onInputChange}
          />
        </div>
        <div>
          <label htmlFor="released">Released:</label>
          <input
            name="released"
            value={videogame.released}
            type="text"
            onChange={onInputChange}
          />
        </div>
        <div>
          {genres.map((genre) => {
            return (
              <div key={genre.id}>
                <button
                  value={genre.id}
                  name={genre.name}
                  onClick={(e) => addGenres(e, genre.id)}
                >
                  x
                </button>
                {genre.name}
              </div>
            );
          })}
        </div>
        <input type="submit" />
      </form>
    </div>
  );
}
