import axios from "axios";
import { VIDEOGAMES_URL } from "../constants";
import { GET_VIDEOGAMES } from "./constants";

export function getVideogames() {
  return function (dispatch) {
    return axios
      .get(VIDEOGAMES_URL)
      .then((videogames) => {
        dispatch({
          type: GET_VIDEOGAMES,
          payload: videogames.data,
        });
      })
      .catch((error) => {
        return { msg: error };
      });
  };
}
